@media screen and (max-width: 850px) {
  .logo{
      font-size: 31px;
  }
  .navbar{
      position: absolute;
      width: 100%;
      height: 100vh;
      padding: 40px 50px;
      top: 0;
      right: 0;
      bottom: 0;
      left: 100%;
      display: flex;
      flex-direction: column;
      background: #2d2d2d;
      transition: all 0.4s ease-in-out;
  }
  .navbar a{
      display: block;
      color: #c3c3c3;
      padding: 0px;
      margin: 0px 0px 30px 0px;
      font-size: 1.5rem;
      font-weight: 400;
  }
  .navbar.open{
      left: 0;
  }
  .social i{
      height: 35px;
      width: 35px;
      font-size: 17px;
  }
  .skill{
      margin: 10px 0;
  }
}

@media screen and (max-width: 500px) {
  :root{
      --big-font: 4rem;
      --p-font: 15px;
      transition: .2s;
  }
  .btn{
      padding: 9px 28px;
  }
}